// TicketHeader.jsx
import { MessageSquare, Plus } from "lucide-react";

export default function TicketHeader({ onNewTicket }) {
  return (
    <div className="flex items-center justify-between mb-6">
      <h1 className="text-lg lg:text-2xl font-bold flex items-center gap-2">
        <MessageSquare />
        تیکت های پشتیبانی
      </h1>
      <button
        onClick={onNewTicket}
        className="flex items-center gap-2 bg-purple-700 hover:bg-purple-600 text-white px-4 py-2 rounded-lg transition-colors"
      >
        <Plus size={18} />
        تیکت جدید
      </button>
    </div>
  );
}
