// TicketSystem.jsx - Updated with ticket detail view functionality
import { useState, useEffect } from "react";
import TicketHeader from "./TicketHeader";
import TicketSearch from "./TicketSearch";
import TicketTable from "./TicketTable";
import NewTicketForm from "./NewTicketForm";
import TicketDetailView from "./TicketDetailView";
import { useNavigate } from "react-router-dom";

export default function TicketSystem() {
  const [tickets, setTickets] = useState([]);
  const [filteredTickets, setFilteredTickets] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [statusFilter, setStatusFilter] = useState("all");
  const [showNewTicketForm, setShowNewTicketForm] = useState(false);
  const [selectedTicketId, setSelectedTicketId] = useState(null);
  const navigate = useNavigate();

  // Mock data for demonstration
  useEffect(() => {
    const API_URL =
      window.location.hostname === "localhost"
        ? "http://127.0.0.1:8000/api/tickets/"
        : "https://allmaxsport.com/api/tickets/";

    async function loadTickets() {
      const resposne = await fetch(API_URL);
      const data = await resposne.json();
      setTickets(data);
      setFilteredTickets(data);
    }

    loadTickets();
  }, []);

  // Filter tickets based on search term and status
  useEffect(() => {
    let result = tickets;

    if (searchTerm) {
      result = result.filter(
        (ticket) =>
          ticket?.subject?.includes(searchTerm) ||
          ticket?.message?.includes(searchTerm)
      );
    }

    if (statusFilter !== "all") {
      result = result.filter((ticket) => ticket?.status === statusFilter);
    }

    setFilteredTickets(result);
  }, [searchTerm, statusFilter, tickets]);

  // Add new ticket to the list
  const handleCreateTicket = (newTicket) => {
    const ticket = {
      id: tickets.length + 1,
      ...newTicket,
      status: "open",
      date: "1404/01/31", // Current date in Persian calendar format
      replies: 0,
    };

    setTickets([ticket, ...tickets]);
    setShowNewTicketForm(false);
  };

  // Handle opening a ticket detail
  function handleOpenTicket(ticketId) {
    navigate(`/dashboard/tickets/${Number(ticketId)}`);
  }

  // Render ticket detail view or main ticket list
  if (selectedTicketId !== null) {
    return (
      <div className="flex flex-col bg-gray-900 text-gray-100 rounded-lg shadow-lg p-6 max-w-6xl mx-auto">
        <TicketDetailView />
      </div>
    );
  }

  return (
    <div
      className="flex flex-col bg-zinc-900 text-gray-100 rounded-lg shadow-lg p-6 max-w-6xl mx-auto"
      dir="rtl"
    >
      <TicketHeader
        onNewTicket={() => setShowNewTicketForm(!showNewTicketForm)}
      />

      {showNewTicketForm && (
        <NewTicketForm
          onSubmit={handleCreateTicket}
          onCancel={() => setShowNewTicketForm(false)}
        />
      )}

      <TicketSearch
        searchTerm={searchTerm}
        setSearchTerm={setSearchTerm}
        statusFilter={statusFilter}
        setStatusFilter={setStatusFilter}
      />

      <TicketTable tickets={filteredTickets} onOpenTicket={handleOpenTicket} />
    </div>
  );
}
