// Enhanced TicketTable.jsx with responsive design
import { useState, useEffect } from "react";
import { MessageSquare, MoreVertical, ChevronDown } from "lucide-react";
import StatusBadge from "./StatusBadge";
import PriorityBadge from "./PriorityBadge";

export default function TicketTable({ tickets, onOpenTicket }) {
  const [windowWidth, setWindowWidth] = useState(
    typeof window !== "undefined" ? window.innerWidth : 0
  );

  // Track window resize for responsive behavior
  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };

    if (typeof window !== "undefined") {
      window.addEventListener("resize", handleResize);
      return () => window.removeEventListener("resize", handleResize);
    }
  }, []);

  // Determine if we're on mobile
  const isMobile = windowWidth < 768;

  // For mobile: expanded row state
  const [expandedId, setExpandedId] = useState(null);

  const toggleRow = (id, e) => {
    e.stopPropagation();
    setExpandedId(expandedId === id ? null : id);
  };

  // Mobile card view component
  const MobileTicketCard = ({ ticket }) => (
    <div
      className="bg-zinc-800 border border-zinc-700 rounded-lg mb-3 overflow-hidden"
      onClick={() => onOpenTicket(ticket.id)}
    >
      <div className="p-4">
        <div className="flex justify-between items-start mb-2">
          <h3 className="text-sm font-medium flex-1">{ticket.subject}</h3>
          <button
            onClick={(e) => toggleRow(ticket.id, e)}
            className="text-gray-400 p-1 rounded-full hover:bg-gray-700 transition-colors ml-2"
          >
            <ChevronDown
              size={18}
              className={`transform transition-transform ${
                expandedId === ticket.id ? "rotate-180" : ""
              }`}
            />
          </button>
        </div>

        <div className="flex items-center justify-between mb-2">
          <StatusBadge status={ticket.status} />
          <PriorityBadge priority={ticket.priority} />
        </div>

        <div className="text-xs text-gray-400">
          {new Date(ticket.updated_at).toLocaleDateString("fa-IR")}
        </div>

        {expandedId === ticket.id && (
          <div className="mt-3 pt-3 border-t border-zinc-700">
            <p className="text-xs text-gray-400 mb-2">{ticket.message}</p>
            <div className="flex justify-between">
              <span className="flex items-center gap-1 text-xs">
                <MessageSquare size={14} />
                {ticket?.messages?.length} پاسخ
              </span>
              <button
                className="text-gray-400 hover:text-gray-300 p-1"
                onClick={(e) => {
                  e.stopPropagation();
                  // Additional actions
                }}
              >
                <MoreVertical size={16} />
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );

  // Desktop table view
  const DesktopTicketTable = () => (
    <div className="rounded-lg overflow-hidden border border-gray-700">
      <table className="min-w-full divide-y divide-zinc-700">
        <thead className="bg-zinc-800">
          <tr>
            <th
              scope="col"
              className="px-6 py-4 text-right text-xs font-medium text-gray-400 uppercase tracking-wider"
            >
              تیکت
            </th>
            <th
              scope="col"
              className="px-6 py-4 text-right text-xs font-medium text-gray-400 uppercase tracking-wider"
            >
              وضعیت
            </th>
            <th
              scope="col"
              className="px-6 py-4 text-right text-xs font-medium text-gray-400 uppercase tracking-wider"
            >
              اولویت
            </th>
            <th
              scope="col"
              className="px-6 py-4 text-right text-xs font-medium text-gray-400 uppercase tracking-wider"
            >
              تاریخ
            </th>
            <th
              scope="col"
              className="px-6 py-4 text-right text-xs font-medium text-gray-400 uppercase tracking-wider"
            >
              پاسخ‌ها
            </th>
            <th scope="col" className="relative px-6 py-3">
              <span className="sr-only">عملیات</span>
            </th>
          </tr>
        </thead>
        <tbody className="bg-zinc-800 divide-y divide-zinc-700">
          {tickets.map((ticket) => (
            <tr
              key={ticket.id}
              className="hover:bg-gray-750 cursor-pointer transition-colors"
              onClick={() => onOpenTicket(ticket.id)}
            >
              <td className="px-6 py-4">
                <div>
                  <h3 className="text-sm font-medium">{ticket.subject}</h3>
                  <p className="text-xs text-gray-400 truncate max-w-xs">
                    {ticket.message}
                  </p>
                </div>
              </td>
              <td className="px-6 py-4">
                <StatusBadge status={ticket.status} />
              </td>
              <td className="px-6 py-4">
                <PriorityBadge priority={ticket.priority} />
              </td>
              <td className="px-6 py-4 text-sm">
                {new Date(ticket.updated_at).toLocaleDateString("fa-IR")}
              </td>
              <td className="px-6 py-4 text-sm">
                <span className="flex items-center gap-1">
                  <MessageSquare size={14} />
                  {ticket?.messages?.length}
                </span>
              </td>
              <td className="px-6 py-4 text-left text-sm font-medium">
                <button
                  className="text-gray-400 hover:text-gray-300 p-1 rounded hover:bg-gray-700"
                  onClick={(e) => {
                    e.stopPropagation(); // Prevent row click event
                    // Additional actions like deleting ticket or other quick actions
                  }}
                >
                  <MoreVertical size={18} />
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );

  // Empty state component
  const EmptyState = () => (
    <div className="text-center py-8 bg-zinc-800 rounded-lg border border-gray-700">
      <p className="text-gray-400">
        هیچ تیکتی یافت نشد. فیلترها را تنظیم کنید یا یک تیکت جدید ایجاد کنید.
      </p>
    </div>
  );

  // Main render
  return (
    <div className="w-full" dir="rtl">
      {tickets.length > 0 ? (
        isMobile ? (
          <div className="space-y-3">
            {tickets.map((ticket) => (
              <MobileTicketCard key={ticket.id} ticket={ticket} />
            ))}
          </div>
        ) : (
          <div className="overflow-x-auto">
            <DesktopTicketTable />
          </div>
        )
      ) : (
        <EmptyState />
      )}
    </div>
  );
}
