// NewTicketForm.jsx
import { useState, useEffect } from "react";
import toast from "react-hot-toast";

const getUserIdFromCookies = () => {
  const cookies = document.cookie.split(";");
  for (let cookie of cookies) {
    const [name, value] = cookie.trim().split("=");
    if (name === "userId") {
      return value;
    }
  }
  return null;
};

export default function NewTicketForm({ onSubmit, onCancel }) {
  const [newTicket, setNewTicket] = useState({
    subject: "",
    message: "",
    priority: "medium",
    related_order_id: "",
  });

  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState(null);
  const [userInfo, setUserInfo] = useState(null);

  const TICKETS_API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/tickets/"
      : "https://allmaxsport.com/api/tickets/";

  const USER_API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/user/"
      : "https://allmaxsport.com/api/user/";

  // Fetch user information when component mounts
  useEffect(() => {
    const userId = getUserIdFromCookies();
    const fetchUserInfo = async () => {
      try {
        const response = await fetch(`${USER_API_URL}?id=${userId}`);
        if (!response.ok) return;

        const data = await response.json();
        let userData = data[0];
        setUserInfo(userData);
        toast.success("data fetched successfully!");
      } catch (err) {
        console.error("Error fetching user info:", err);
      }
    };

    fetchUserInfo();
  }, []);

  const handleSubmit = async () => {
    // Validate form
    if (!newTicket.subject || !newTicket.message) {
      setError("لطفا موضوع و پیام را وارد کنید");
      return;
    }

    // Get user ID from cookies
    const userId = getUserIdFromCookies();
    if (!userId) {
      setError("اطلاعات کاربری یافت نشد. لطفا دوباره وارد شوید");
      return;
    }

    try {
      setIsSubmitting(true);
      setError(null);
      // Prepare customer name from user info
      const customerName = userInfo
        ? `${userInfo.first_name} ${userInfo.last_name}`
        : "کاربر ناشناس";

      // Step 1: Create a new ticket with customer information
      const ticketResponse = await fetch(TICKETS_API_URL, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          subject: newTicket.subject,
          priority: newTicket.priority,
          status: "open",
          customer: userId,
          customer_name: customerName,
          related_order_id: newTicket.related_order_id || null,
          created_at: new Date().toISOString(),
          updated_at: new Date().toISOString(),
        }),
      });

      if (!ticketResponse.ok) {
        throw new Error(`خطا در ایجاد تیکت: ${ticketResponse.status}`);
      }

      const ticketData = await ticketResponse.json();

      // Step 2: Add the initial message to the ticket
      const messageResponse = await fetch(
        `${TICKETS_API_URL}?id=${ticketData.id}`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            sender: "customer",
            text: ticketData.subject,
            message: newTicket.message,
            ticket: ticketData.id,
            timestamp: new Date().toISOString(),
          }),
        }
      );

      if (!messageResponse.ok) {
        throw new Error(`خطا در ارسال پیام: ${messageResponse.status}`);
      }

      // Call the onSubmit callback with the created ticket data
      onSubmit(ticketData);

      // Reset form
      setNewTicket({
        subject: "",
        message: "",
        priority: "medium",
        related_order_id: "",
      });
    } catch (err) {
      console.error("Error submitting ticket:", err);
      setError(err.message || "خطا در ارسال تیکت");
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="bg-zinc-800 p-4 rounded-lg mb-6">
      <h2 className="text-lg font-semibold mb-4">ایجاد تیکت جدید</h2>

      {error && (
        <div className="bg-red-900/50 border border-red-700 p-3 rounded-md mb-4 text-red-100">
          {error}
        </div>
      )}

      <div className="space-y-4">
        <div>
          <label className="block text-sm font-medium mb-1">موضوع</label>
          <input
            type="text"
            value={newTicket.subject}
            onChange={(e) =>
              setNewTicket({ ...newTicket, subject: e.target.value })
            }
            className="w-full bg-zinc-700 border border-gray-600 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-purple-500"
            placeholder="توضیح مختصر مشکل"
            disabled={isSubmitting}
          />
        </div>

        <div>
          <label className="block text-sm font-medium mb-1">
            شماره‌ی سفارش
          </label>
          <input
            type="text"
            value={newTicket.related_order_id}
            onChange={(e) =>
              setNewTicket({ ...newTicket, related_order_id: e.target.value })
            }
            className="w-full bg-zinc-700 border border-gray-600 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-purple-500"
            placeholder="مثال: ORD12345"
            disabled={isSubmitting}
          />
        </div>

        <div>
          <label className="block text-sm font-medium mb-1">پیام</label>
          <textarea
            value={newTicket.message}
            onChange={(e) =>
              setNewTicket({ ...newTicket, message: e.target.value })
            }
            className="w-full bg-zinc-700 border border-gray-600 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-purple-500 min-h-32"
            placeholder="توضیح کامل مشکل خود"
            disabled={isSubmitting}
          />
        </div>

        <div className="flex flex-col sm:flex-row gap-4">
          <div className="flex-1">
            <label className="block text-sm font-medium mb-1">اولویت</label>
            <select
              value={newTicket.priority}
              onChange={(e) =>
                setNewTicket({ ...newTicket, priority: e.target.value })
              }
              className="w-full bg-zinc-700 border border-gray-600 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-purple-500"
              disabled={isSubmitting}
            >
              <option value="low">عادی</option>
              <option value="medium">متوسط</option>
              <option value="high">فوری</option>
            </select>
          </div>
          <div className="flex-1 flex items-end">
            <div className="flex gap-2 w-full">
              <button
                onClick={onCancel}
                className="flex-1 bg-zinc-700 hover:bg-gray-600 px-4 py-2 rounded-lg transition-colors disabled:opacity-50"
                disabled={isSubmitting}
              >
                انصراف
              </button>
              <button
                onClick={handleSubmit}
                className="flex-1 bg-purple-700 hover:bg-purple-600 px-4 py-2 rounded-lg transition-colors disabled:opacity-50"
                disabled={isSubmitting}
              >
                {isSubmitting ? "در حال ارسال..." : "ثبت تیکت"}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
