// TicketDetailView.jsx - Updated with auto-scrolling functionality
import { useState, useEffect, useRef } from "react";
import { ArrowRight, Send, Paperclip } from "lucide-react";
import StatusBadge from "./StatusBadge";
import PriorityBadge from "./PriorityBadge";
import { useParams, Link } from "react-router-dom";
import Loader from "../../../Loader";

export default function TicketDetailView() {
  const { ticketId } = useParams();
  const [ticket, setTicket] = useState(null);
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState("");
  const [isLoading, setIsLoading] = useState(true);

  // Reference to the message container for scrolling
  const messagesEndRef = useRef(null);
  const messagesContainerRef = useRef(null);

  // Scroll to bottom of messages
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };
  const API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/tickets/"
      : "https://allmaxsport.com/api/tickets/";

  const updateTicketStatus = async (ticketId, newStatus) => {
    setTicket((ticket) => ({
      ...ticket,
      status: newStatus,
    }));

    // Send PATCH request to API
    fetch(`${API_URL}?id=${ticketId}`, {
      method: "PATCH",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ status: newStatus }),
    }).catch((error) => console.error("Failed to update ticket:", error));
  };

  // Fetch ticket details
  useEffect(() => {
    // Mock API call - replace with your actual API
    setIsLoading(true);

    async function loadTicketDetails() {
      try {
        const response = await fetch(`${API_URL}?id=${ticketId}`);
        const data = await response.json();
        setTicket(data[0]);
        setMessages(data[0].messages);
      } catch (e) {
        console.log(e.message);
      } finally {
        setIsLoading(false);
      }
    }

    loadTicketDetails();
  }, [ticketId]);

  // Scroll to bottom when messages change or when component mounts
  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  // Handle sending a new message
  const handleSendMessage = async () => {
    if (!newMessage.trim()) return;

    const newMsg = {
      id: messages?.length + 1,
      ticket: ticket.id,
      text: newMessage,
      sender: "customer",
      message: newMessage,
      timestamp: new Date().toISOString(),
      isUser: false,
    };

    // Local update
    setMessages((prev) => [...prev, newMsg]);
    setNewMessage("");

    try {
      await fetch(`${API_URL}?id=${ticket.id}`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(newMsg),
      });
    } catch (e) {
      console.error("Failed to send message:", e);
    }
  };

  // Handle keyboard shortcut for sending message
  const handleKeyDown = (e) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  if (isLoading) {
    return (
      <Loader
        size={40}
        color="text-blue-500"
        center={true}
        dir="row"
        className="text-white"
      >
        درحال بارگذاری
      </Loader>
    );
  }

  return (
    <div className="bg-zinc-900 rounded-lg overflow-hidden">
      {/* Ticket Header */}
      <div className="p-4 bg-zinc-800 border-b border-zinc-700">
        <div className="flex items-center mb-4">
          <Link to={"/dashboard/tickets"}>
            <button className="text-gray-400 hover:text-white flex items-center mr-2">
              <ArrowRight size={20} />
              <span className="mr-1">بازگشت</span>
            </button>
          </Link>
        </div>

        <div className="flex flex-col md:flex-row md:items-center md:justify-between">
          <div>
            <h2 className="text-xl text-gray-200 font-semibold mb-1">
              {ticket.subject}
            </h2>
            <p className="text-sm text-gray-400">
              شناسه تیکت: #{ticket.id} | تاریخ ایجاد: {ticket.date} | ارسال
              کننده: {ticket.customer_name}
            </p>
          </div>

          <div className="flex flex-wrap gap-2 mt-2 md:mt-0">
            <StatusBadge status={ticket?.status} />
            <PriorityBadge priority={ticket?.priority} />

            {/* Status Change Dropdown */}
            <select
              className="bg-zinc-700 border border-zinc-600 rounded-md text-gray-300 text-xs px-2 py-1"
              value={ticket.status}
              onChange={(e) => {
                updateTicketStatus(ticket.id, e.target.value);
              }}
            >
              <option className="text-gray-300" value="" disabled>
                تغییر وضعیت
              </option>
              <option value="open">باز</option>
              <option value="pending">درحال بررسی</option>
              <option value="closed">بسته شده</option>
            </select>
          </div>
        </div>
      </div>

      {/* Message List */}
      <div
        ref={messagesContainerRef}
        className="p-4 h-96 overflow-y-auto bg-zinc-900 flex flex-col"
      >
        {messages.map((msg) => {
          const isCustomer = msg.sender === "customer";
          const formattedName = isCustomer ? "شما" : "مدیر";
          const formattedDate = new Date(msg?.timestamp).toLocaleDateString(
            "fa-IR"
          );

          return (
            <div
              key={msg.id}
              className={`mb-4 flex ${
                isCustomer ? "justify-end" : "justify-start"
              }`}
            >
              <div
                className={`relative max-w-md p-4 rounded-xl shadow-md ${
                  isCustomer
                    ? "bg-blue-600 text-white rounded-br-none"
                    : "bg-zinc-700 text-gray-100 rounded-bl-none"
                }`}
              >
                {/* Name & Timestamp (always in a row, spaced apart) */}
                <div className="flex justify-between gap-4 items-center mb-2 text-xs opacity-80">
                  <span className="font-semibold">{formattedName}</span>
                  <span className="text-[11px]">{formattedDate}</span>
                </div>

                {/* Message Text */}
                <p className="text-sm whitespace-pre-wrap">{msg.message}</p>

                {/* Attachments if any */}
                {msg.attachments?.length > 0 && (
                  <div className="mt-3 flex flex-wrap gap-2">
                    {msg.attachments.map((attachment, idx) => (
                      <div
                        key={idx}
                        className="bg-zinc-600 text-xs rounded px-2 py-1 flex items-center"
                      >
                        <Paperclip size={12} className="inline-block mr-1" />
                        {attachment.file_name}
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>
          );
        })}

        {/* This empty div is used as a reference point to scroll to */}
        <div ref={messagesEndRef} />
      </div>

      {/* Message Input */}
      <div className="p-4 bg-zinc-800 border-t border-zinc-700">
        <div className="flex items-center justify-center gap-2">
          <div className="flex-1">
            <textarea
              value={newMessage}
              onChange={(e) => {
                setNewMessage(e.target.value);
              }}
              // onFocus={() => markPendingIfNeeded(ticketId)} // Optional: only trigger on focus
              onKeyDown={handleKeyDown}
              className="w-full bg-zinc-700 border border-zinc-600 rounded-lg p-3 focus:outline-none focus:ring-2 focus:ring-purple-500 min-h-24 resize-none text-gray-300 placeholder:text-gray-400"
              placeholder="پیام خود را بنویسید..."
            />
          </div>
          <div className="flex flex-col gap-2">
            <button
              className="bg-zinc-700 hover:bg-zinc-600 p-2 rounded-lg text-gray-300"
              title="پیوست فایل"
            >
              <Paperclip size={20} />
            </button>
            <button
              onClick={handleSendMessage}
              disabled={!newMessage.trim()}
              className={`p-2 rounded-lg ${
                !newMessage.trim()
                  ? "bg-gray-700 text-gray-500 cursor-not-allowed"
                  : "bg-purple-700 hover:bg-purple-600 text-white"
              }`}
              title="ارسال پیام"
            >
              <Send size={20} />
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
