import { AlertTriangle, Clock, CheckCircle } from "lucide-react";

export default function StatusBadge({ status }) {
  const statusConfig = {
    open: {
      icon: <AlertTriangle size={14} />,
      color: "bg-amber-700 text-amber-200",
      label: "باز",
    },
    pending: {
      icon: <Clock size={14} />,
      color: "bg-gray-700 text-gray-200",
      label: "در حال بررسی",
    },
    closed: {
      icon: <CheckCircle size={14} />,
      color: "bg-green-800 text-green-200",
      label: "بسته شده",
    },
  };

  // Fallback config for unknown status
  const config = statusConfig[status] || {
    icon: <AlertTriangle size={14} />,
    color: "bg-red-700 text-white",
    label: "نامشخص",
  };

  return (
    <span
      className={`flex items-center gap-1 px-2 py-1 rounded-full text-xs font-medium ${config.color}`}
    >
      {config.icon}
      {config.label}
    </span>
  );
}
